/* Login Page Start*/
.outer-login {
    display: table;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #000b29;
    background-repeat: no-repeat;
    background-position: center; 
    background-size: auto;
  }
  
  .middle-login {
    display: table-cell;
    vertical-align: middle;
  }
  
  .inner-login {
    margin-top: 30%; 
    margin-left: auto;
    margin-right: auto;
    /* width: 400px; */
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    color: #6B778C;
    font-size: 16px;
    background: #fff;
    /* line-height: 50px; */
    padding: 10px 25px;
    width: 100%;
    /*whatever width you want*/
  }

  .logo-image{
    background-image: url("https://thedecorshop.s3.ap-south-1.amazonaws.com/web-images/logo/thedecorshop-logo-transparent.png");
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 250px;
    height: 200px;

}

.forget-password-message{
    font-size: 15px;
    padding: 15px 0px;
    cursor: pointer;
}

.login-button{
    text-align: right
}

.or-message{
    text-align: center;
    padding: 10px;
}

.magic-link-button{
    text-align: center;
    padding: 10px;
}

.forget-password-message:hover{
    color:#EEB83C;
}

.field-div{
    margin-top: 20px;
}

/* Login Page End */

/* Dashboard start */
.logo-side-navbar{
    height: 120px;
}

.dashboard-page{
    padding: 4% 2%;
    min-height: 50em;
}


/* Dashboard End */


/* Pagination Start */

ul.pagination {
    display: inline-block;
    padding-left: 4px;
    padding-right: 15px;
    /* padding-top: 10px; */
}

.pagination li {
    border-radius: 3px;
    border-width: 0px;
    display: inline-block;  
    cursor: pointer;
    margin: 2px;
    padding: 0px 8px;
    transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
    height: 35px;
    width: 40px;
    line-height: 34px;
    text-align: center;
    font-weight: 500;
    font-size: 16px;
}

.pagination li a{
    color: #6B778C;  
    border-radius: 3px;
    border-width: 0px;
    display: inline-block;  
    cursor: pointer;
    margin: 2px;
    padding: 2px 10px;
    transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
    height: 35px;
    width: 40px;
    line-height: 34px;
    text-align: center;
    font-weight: 500;
    font-size: 16px;


}
.pagination li.active a{
    color: rgb(244, 245, 247) !important;
    background: rgb(37, 56, 88);
    border-radius: 3px;
    border-width: 0px;
    margin: 0px;
    padding: 2px 10px;
    text-decoration: none;
    transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
    outline: none !important;
    cursor: pointer;
    height: 35px;
    width: 40px;
    line-height: 34px;
    text-align: center;
    font-weight: 500;
    font-size: 16px;
}
/* .pagination li.active{
} */

.pagination li a:hover {
    background: #eaeaea;
}

.pagination li.active a:hover{
    background: rgb(37, 56, 88);
}
.pagination-next a{
    font-weight: 700;
    outline: none !important;
}




/* Pagination End */

