    font-family: sans-serif;
    /* margin:10px 0px; */
}
.dropzone{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FAFBFC;
    border-color: #DFE1E6;
    border-radius: 3px;
    box-sizing: border-box;
    border-width: 2px;
    border-style: dashed;
    padding: 6px 6px;
    font-size: 14px;
    color: #bdbdbd;
    outline: none;
    height: 50px;
    line-height: 50px;
    transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}

.image-div{
